import { RenderWithLogic } from "../../helpers/RenderWithLogic";
import { useFocus } from "./useFocus";

export const UseFocusExample = () => (
  <RenderWithLogic
    render={() => {
      const [ref, focus] = useFocus();

      return (
        <div>
          <button onClick={focus}>Focus!</button>
          <input ref={ref} />
        </div>
      );
    }}
  />
);
